<template>
<el-row class="mainWrapper loginWrapper" :gutter="10">
    <el-col :span="12" class="logo">
        <el-image style="width: 100px; height: 100px">
            <div slot="error" class="image-slot">
                <i class="el-icon-picture-outline"></i>
            </div>
        </el-image>
    </el-col>
    <el-col :span="12" class="loginForm">
        <el-card>
            <div class="login_title">{{ $t('login.loginTitle') }}</div>
            <lang-icon></lang-icon>
            <el-form ref="form" :model="form" :rules="rules">
                <el-form-item prop="email" :error="nameErrMsg">
                    <el-input v-model="form.email" placeholder="E-mail" prefix-icon="el-icon-user">
                    </el-input>
                </el-form-item>
                <el-form-item prop="password" :error="errMsg">
                    <el-input v-model="form.password" prefix-icon="el-icon-lock" placeholder="password" show-password></el-input>
                </el-form-item>
            </el-form>
            <el-row :gutter="10">
                <el-col :span="12">
                    <el-checkbox v-model="form.status" @change="changeCheckBoxStatus">
                        <p class="text">{{ $t('login.remember') }}</p>
                    </el-checkbox>
                </el-col>
                <el-col :span="12">
                    <router-link class="text forgetLink" to="forgetpassword">{{
              $t('login.forget')
            }}</router-link>
                </el-col>
            </el-row>
            <el-button type="primary" :loading="isLogin" class="loginBtn" @click="submitForm">{{ $t('btn.login') }}</el-button>
        </el-card>
    </el-col>
</el-row>
</template>

<script>
import {
    userAPI
} from '@/api';
import LangIcon from '@/components/LangIcon';
export default {
    mounted() {
        userAPI.getAllShop();
        this.getAllUserInfo();
        // 存在cookie就显示邮箱
        if (this.$Cookies.get('token')) {
            this.form.email = this.$Cookies.get('login_email');
            this.form.password = '********';
            this.form.status = !!this.$Cookies.get('checkBoxStatus');
        }
    },

    components: {
        LangIcon,
    },
    data() {
        return {
            form: {
                email: '',
                password: '',
                status: false,
            },
            rules: {
                email: [{
                    required: true,
                    message: this.$t('login.emailErrMsg'),
                    trigger: 'blur',
                }, ],
                password: [{
                    required: true,
                    message: this.$t('login.pswErrMsg'),
                    trigger: 'blur',
                }, ],
            },
            errMsg: '',
            nameErrMsg: '',
            isLogin: false,
        };
    },

    methods: {
        submitForm() {
            this.$refs['form'].clearValidate();
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    this.isLogin = true;
                    const data = {
                        email: this.form.email,
                        password: this.form.password,
                    };
                    this.$store.dispatch('user/login', data).then(
                        () => {
                            this.isLogin = false;
                        },
                        (err) => {
                            if (err) {
                                this.errMsg = '';
                                this.nameErrMsg = '';
                                this.$nextTick(() => {
                                    if (err.status === 401) {
                                        this.errMsg = this.$t('login.userNameErrMsg');
                                        this.nameErrMsg = ' ';
                                    } else {
                                        this.errMsg = this.$t('login.userNameErrMsg');
                                        this.nameErrMsg = ' ';
                                    }
                                });
                                this.isLogin = false;
                            }
                        }
                    );
                } else {
                    return false;
                }
            });
        },
        changeCheckBoxStatus(value) {
            if (value) {
                this.$Cookies.set('checkBoxStatus', value, {
                    expires: 7,
                });
            } else {
                this.$Cookies.remove('checkBoxStatus');
            }
        },
        getAllUserInfo() {
            const data = {
                operate_mode: 'get_login_list',
            };
            userAPI.loginOperate(data);
        },
        async forgetPassword() {
            let email = this.$store.state.user.data.email;
            await userAPI.forgetPassword({
                email: email,
            });
            let obj = {
                email: this.$store.state.user.data.email,
                password: this.target.value,
            };
            await userAPI.forgetPassword2(obj);
        },
    },
};
</script>

<style lang="scss" scoped>
.mainWrapper {
    height: 100%;
    width: 100%;
}

.login_title {
    display: inline-block;
    width: 100%;
    margin-left: -30px;
    text-align: center;
    padding-bottom: 1em;
}

.text {
    font-size: 12px;
}

.loginBtn {
    margin-left: 10%;
    margin-top: 10%;
    width: 80%;
}
</style>
